<template>
  <header>
    <nav>
       <div class="logo"> <a href="#"><img src="./../img/logo.jpg" alt="fleur d'oranger"></a> </div>
      <ul 
      :class="{ok : this.rideau.active}" class="active">
        <li :class="{inactiveLi : !this.rideau.active}"> <a href="#services">services</a>  </li>
        <li :class="{inactiveLi : !this.rideau.active}"> <a href="#masseuse">masseuse</a> </li>
        <li :class="{inactiveLi : !this.rideau.active}"> <a href="#contact">contactes</a> </li>
        <li :class="{inactiveLi : !this.rideau.active}"> <a href="#massages">massages</a> </li>
      </ul>

      <div class="resp-menu" @click="change()"
      
      >  
        <div class="bton" ></div>
        <div class="bton" ></div>
        <div class="bton" ></div>
      </div>
    </nav>
  </header>
  
</template>
  
  <script>
  export default {
    name: "nav-bar",
    data(){
      return{
            rideau: {
              active: false
            }
           
      }
      },
      methods:{

        change(){
          this.rideau.active = !this.rideau.active
        }
       
      
    }

    }
  </script>
  
  <style>
  
  nav {
    width: 100%;
    height: 70px;
    position: absolute;
    color: white;
    z-index: 20;
    display: flex;
  }
 
  
  header nav ul{
    width: 60%;
    display: flex;
    align-items: center;
    margin: 0 auto;
    position: relative;
    justify-content: end
  }

  header nav ul li{
    margin-left: 3%;
    list-style: none;
    margin-top: 30px;
    position: relative;
  }
  header nav ul li:nth-child(5){
    margin-right: 55px;
  }

  header nav ul li::after{
  content:"";
  display: block;
  width: 100%;
  position: absolute;
  height: 3px;
  right: 100%;
  bottom: -3px;
  background-color:rgb(163, 114, 41); 
  opacity: 0;
  }

  header nav ul li:hover::after{
    display: block;
    opacity: 1;
    right: 0px;
    transition: .3s;
  }

  header a{
    color: white;
    text-decoration: none;
  }

  header nav img{
    width: 100%;
    height: 100%;
  }
  .logo{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    margin-top: 20px;
    margin-left: 100px;
  }

  .resp-menu{
    width:60px;
    height: 60px;
    border-radius: 10px;
    background-color: transparent ;
    margin-right: 50px;
    margin-top: 20px;
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    overflow: hidden;
    position: relative;
  }

  .bton{
    width: 80%;
    height: 3px;
    background-color: white;
  }
 

  @media screen and (max-width: 900px){
    header nav ul{
      display: none
    }
    .inactiveLi{
       display: none
    }
    .active{
      position: absolute;
      display: flex;
      margin: 0 auto;
      flex-direction: column;
      background-color:rgb(163, 114, 41); 
      height: 100vh;
      width: 0%;
      right: 0%;
      justify-content: center;
      transition: .6s;
    }
    .ok{
      width: 100%;
    }

    nav{
      justify-content: space-between;
    }
    .resp-menu{
    display: flex;
    }

    header nav ul li::after{
      background-color: white;
    }
  }

  </style>
  