<template>
     <div id="inscription">
        <h2>inscription</h2>
        <div class="container">
          <form action="">
            <div>
            <template  v-if="this.nom"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><!--! Font Awesome Pro 6.3.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d="M243.8 339.8C232.9 350.7 215.1 350.7 204.2 339.8L140.2 275.8C129.3 264.9 129.3 247.1 140.2 236.2C151.1 225.3 168.9 225.3 179.8 236.2L224 280.4L332.2 172.2C343.1 161.3 360.9 161.3 371.8 172.2C382.7 183.1 382.7 200.9 371.8 211.8L243.8 339.8zM512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM256 48C141.1 48 48 141.1 48 256C48 370.9 141.1 464 256 464C370.9 464 464 370.9 464 256C464 141.1 370.9 48 256 48z"/></svg></template>
            <input @input="security(); nomFct()" type="text" name="nom" placeholder="nom" v-model="this.profil.nom">
            <label for="text"></label>
          </div>
            <div>
            <template  v-if="this.tel"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><!--! Font Awesome Pro 6.3.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d="M243.8 339.8C232.9 350.7 215.1 350.7 204.2 339.8L140.2 275.8C129.3 264.9 129.3 247.1 140.2 236.2C151.1 225.3 168.9 225.3 179.8 236.2L224 280.4L332.2 172.2C343.1 161.3 360.9 161.3 371.8 172.2C382.7 183.1 382.7 200.9 371.8 211.8L243.8 339.8zM512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM256 48C141.1 48 48 141.1 48 256C48 370.9 141.1 464 256 464C370.9 464 464 370.9 464 256C464 141.1 370.9 48 256 48z"/></svg></template>
            <input @input="security(); telFct()" type="text" name="tel" placeholder="tel" v-model="this.profil.tel">
            <label for="text"></label>
          </div>
          <div>
            <template  v-if="this.massage"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><!--! Font Awesome Pro 6.3.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d="M243.8 339.8C232.9 350.7 215.1 350.7 204.2 339.8L140.2 275.8C129.3 264.9 129.3 247.1 140.2 236.2C151.1 225.3 168.9 225.3 179.8 236.2L224 280.4L332.2 172.2C343.1 161.3 360.9 161.3 371.8 172.2C382.7 183.1 382.7 200.9 371.8 211.8L243.8 339.8zM512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM256 48C141.1 48 48 141.1 48 256C48 370.9 141.1 464 256 464C370.9 464 464 370.9 464 256C464 141.1 370.9 48 256 48z"/></svg></template>
            <input @input="security(); massageFct()" type="text" name="massage" placeholder="type de massage" v-model="this.profil.massage">
            <label for="text"></label>
          </div>
          <div>
            <template  v-if="this.date"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><!--! Font Awesome Pro 6.3.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><path d="M243.8 339.8C232.9 350.7 215.1 350.7 204.2 339.8L140.2 275.8C129.3 264.9 129.3 247.1 140.2 236.2C151.1 225.3 168.9 225.3 179.8 236.2L224 280.4L332.2 172.2C343.1 161.3 360.9 161.3 371.8 172.2C382.7 183.1 382.7 200.9 371.8 211.8L243.8 339.8zM512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM256 48C141.1 48 48 141.1 48 256C48 370.9 141.1 464 256 464C370.9 464 464 370.9 464 256C464 141.1 370.9 48 256 48z"/></svg></template>
            <input @input="security(); dateFct()" type="text" name="date" placeholder="jj / mm / heure " v-model="this.profil.date">
            <label for="text"></label>
          </div>

          <template v-if="this.profil.date">
            <div>
              <input @input="security(); dateFct()" type="radio" name="date" placeholder="jj / mm / heure " v-model="this.profil.domicite">
              <input @input="security(); dateFct()" type="radio" name="date" placeholder="jj / mm / heure " v-model="this.profil.cabinet">
            </div>
           </template>

            <template v-if="!this.locked && nom && tel && massage && date">
            <input @click="security()"  type="submit" value="envoyer">
            </template>
          </form>
        </div>
      </div>
</template>
  
  <script>
  export default {
    name: "app-inscription",
    created(){
       this.locked = true
    },
    data(){
      return {
        profil:{
              nom: null,
              tel: null,
              massage: null,
              date: null,
              domicile: null,
              cabinet: null
            },
        locked: true, 
        nom: false,
        tel: false,
        massage: false,
        date: false
        
        
      }
    },
     methods:{
          nomFct(){
            let lettre  =/^[a-zA-Z\u00C0-\u00FF]*$/
            let res = this.profil.nom.split("")
            if(res.length > 2 && lettre.test(this.profil.nom)){
               this.nom = true
            }
            else{
               this.nom = false
            }
          },
          telFct(){
            let res = this.profil.tel.split("")
            let numb = /^[0-9]+$/
            if(res.length == 10 && numb.test(this.profil.tel)){
               this.tel = true
            }
            else{
               this.tel = false
            }
          },
          massageFct(){
            let lettre  =/^[a-zA-Z\u00C0-\u00FF]*$/
            let res = this.profil.massage.split("")
            if(res.length > 4 && lettre.test(this.profil.massage)){
               this.massage = true
            }
            else{
               this.massage = false
            }
          },
          dateFct(){
            let dte = /^([0-2][0-9]|(3)[0-1])(\/)(((0)[0-9])|((1)[0-2]))(\/)([a-z|0-9])/;
            let res = this.profil.date.split("")
            if(res.length > 6 && dte.test(this.profil.date)){
              console.log(res)
               this.date = true
            }
            else{
               this.date = false
            }
          },


          security(){
           let sec = [this.profil.nom, this.profil.tel, this.profil.massage, this.profil.date]
           
             sec.forEach(el => {
               if(!el || el == null || el == undefined || el === ""){
                 this.locked = true;
               }
               else{
                  this.locked = false;
               }
             });
          },
          send(){
             
          }
     }   
  }
  </script>
  
  <style>

  
#inscription{
  width: 100%;
  height: 90vh;
  background-color: rgb(163, 114, 41);
  position: relative;
 
}
#inscription h2{
  position: absolute;
  color: white;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

 form{
  display: flex;
  justify-content: center;
  flex-direction: column;
  left: 50%;
  transform: translateX(-50%);
  top: 15%;
  position: absolute;
  width: 50%;
}
form input{
  width: 100%;
  margin-top: 5%;
  height: 3rem;
  background-color: transparent;
  border: white solid 2px;
  border-left: none;
  border-right: none;
  border-top: none;
  position: relative;
  text-align: center;
  display: flex;
  color: white
}

::placeholder{
  color: white;
  opacity: 1 !important; 
}

input[type=submit]{
  border: solid white 2px;
  color: rgb(163, 114, 41);
  cursor: pointer;
  border-radius: 30px;
  margin-top: 50px;
  background-color: white;
}

input[type=submit]:hover{
  color: white;
  border: white 3px solid;
  background-color:  rgb(163, 114, 41);
  transition: .2s;
}

form svg {
  position: absolute;
  fill: white;
  width: 3rem;
  height: 40px;
  top: 40%;
}

form div{
  display: flex;
  width: 100%;
  margin-top: 5%;
  position: relative;
}





  </style>
  